import React, { useState } from 'react';
import Headings from './components/Headings';
import Radio from './components/Radio';
import Workout from './components/Workout';
import YourWorkout from './components/YourWorkout';

export default function App() {
    const [selectedOption, setSelectedOption] = useState("chest");
    const [showYourWorkout, setShowYourWorkout] = useState(false);

    const handleOptionChange = (newOption) => {
      setSelectedOption(newOption);
      setShowYourWorkout(false);
    };    
    const handleShowYourWorkout = () => {
        setShowYourWorkout(true);
    };  
    
    return(
        <div className="main-container">
            <Headings className="headings side" />
            <div className='left-side side'>
                <div className="left-upper">
                    <div className="left-header">Choose your workout:</div>
                    <Radio onChange={handleOptionChange} />
                </div>
                <div className='left-lower'>
                    {showYourWorkout ? (
                        <YourWorkout selectedOption={selectedOption} />
                    ) : (
                        <Workout selectedOption={selectedOption} onBuildWorkout={handleShowYourWorkout} />
                    )}
                </div>
            </div>
        </div>
    )
}