import React from "react";
import { motion, AnimatePresence } from "framer-motion"

const Workout = ({ selectedOption, onBuildWorkout }) => {

  const workoutsData = {
    chest: {
      name: "Chest Workout",
      image: "/assets/chest.jpg",
    },
    back: {
      name: "Back Workout",
      image: "/assets/back.jpg",
    },
    shoulders: {
      name: "Shoulders Workout",
      image: "/assets/shoulders.jpg",
    },
    legs: {
      name: "Legs Workout",
      image: "/assets/legs.jpg",
    },
  };

  const workout = workoutsData[selectedOption];

  const handleBuildWorkout = () => {
    onBuildWorkout();
  };

  return (
    <AnimatePresence mode="wait">
      <motion.div
        key={selectedOption}
        style={{
          backgroundImage: workout ? `url(${process.env.PUBLIC_URL + workout.image})` : "none",
          backgroundSize: "cover",
          backgroundRepeat: "no-repeat",
          backgroundPosition: "center center",
          width: "100%",
          height: "100%",
          overflow: "hidden"
        }}
        className="workoutIMG"
        initial={{ opacity: 0, scaleX: 1.5, originX: 1 }} 
        animate={{ opacity: 1, scaleX: 1, originX: 0 }} 
        exit={{ opacity: 0, scaleX: 0.5, originX: 0 }} 
        transition={{ duration: 0.3 }}
      >
        <button onClick={handleBuildWorkout} className="workoutBuilderButton">Build Workout</button>
      </motion.div>
    </AnimatePresence>
  );
};

export default Workout;