import React from "react";

export default function Radio({ onChange }) {
    const [selectedOption, setSelectedOption] = React.useState("chest");
  
    const handleOptionChange = (event) => {
      const selectedValue = event.target.value;
      setSelectedOption(selectedValue);
      onChange(selectedValue);
    };
    return (
      <div className="radio-input">
        <div className="glass">
          <div className="glass-inner"></div>
        </div>
        <div className="selector">
          <div className="choice">
            <div>
              <input type="radio" id="chest" name="number-selector" value="chest" className="choice-circle" 
              checked={selectedOption === "chest"} 
              onChange={handleOptionChange} />
              <div className="ball"></div>
            </div>
            <label className="choice-name" htmlFor="chest">
              Chest
            </label>
          </div>
          <div className="choice">
            <div>
              <input type="radio" id="back" name="number-selector" value="back" className="choice-circle"
              checked={selectedOption === "back"}
              onChange={handleOptionChange} />
              <div className="ball"></div>
            </div>
            <label className="choice-name" htmlFor="back">
              Back
            </label>
          </div>
          <div className="choice">
            <div>
              <input type="radio" id="shoulders" name="number-selector" value="shoulders" className="choice-circle" 
                checked={selectedOption === "shoulders"}
                onChange={handleOptionChange} />
              <div className="ball"></div>
            </div>
            <label className="choice-name" htmlFor="shoulders">
              Shoulders
            </label>
          </div>
          <div className="choice">
            <div>
              <input type="radio" id="legs" name="number-selector" value="legs" className="choice-circle" 
                checked={selectedOption === "legs"}
                onChange={handleOptionChange}
              />
              <div className="ball"></div>
            </div>
            <label className="choice-name" htmlFor="legs">
              Legs
            </label>
          </div>
        </div>
      </div>
    );
  }
