import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';

const YourWorkout = ({ selectedOption }) => {
  const [workout, setWorkout] = useState([]);

  const workoutsData = {
    // CHEST WORKOUT
    chest: {
      mid_chest : ["barbell benchpresses", "dumbbell benchpresses", "machine presses", "smith-machine bench presses"],
      upper_chest : ["incline dumbbell presses", "incline barbell presses", "incline presses on machine", "incline smith-machine presses"],
      lower_chest : ["dips", "cables for lower pecs"],
      inner_chest : ["cable flies", "machine flies"],
      short_head_triceps : ["overhand bent-over cable pushdowns", "barbell skull crushers", "narrow grip smith-machine bench presses"],
      long_head_triceps : ["neutral grip bent-over cable pushdowns", "cable star triceps pushdowns", "dumbbell skull crushers"],
    },
    back: {  
    // BACK WORKOUT
      vertical_exercises : ["lat pulldowns", "assisted pullups", "wide neutral grip lat pulldowns", "lat pulldowns on machine", "narrow neutral lat pulldowns"],
      horizontal_upper_exercises : ["bent over machine rows", "wide grip rows with barbell", "wide grip rows on cable machine", "wide grip rows on machine", "wide grip dumbbell rows"],
      horizontal_lower_exercises : ["neutral rows on machine", "neutral rows on cable machine", "neutral dumbbells rows", "narrow grip barbell rows"],
      pullover_exercises : ["machine pullovers", "cable overhand pullovers", "cable underhand pullovers"],
      short_biceps_exercises : ["arms supported barbell curls", "underhand dumbbells curls", "standing barbell curls", "neutral grip dumbbells curls"],
      long_biceps_exercises : ["cross-body curls", "star curls on cables", "assisted chinups"],
    },
    legs: {  
    //LEGS WORKOUT
      heavyQuads : ["hex machine squats", "barbell squats"],
      lightQuads : ["leg extensions", "leg presses"],
      hamstrings : ["sitting leg curls", "nordic curls", "lying leg curls", "romanian deadlifts", "deadlifts", "sumo deadlifts"],
      calves : ["hex machine calf raises", "sitting calf raises", "calf raises on machine"],
      glutes : ["hip thrust", "hip thrust machine"],
    },
    shoulders: {
    // SHOULDERS WORKOUT
    front_delts : ["arnold presses", "landmine narrow presses", "dumbbell presses", "barbell presses", "smith-machine presses", "donkey balls cable raises for front delts"],
    mid_delts : ["cable lateral raises", "dumbbell lateral raises"],
    rear_delts : ["cable face pulls", "cable one arm rotations for rear delts", "cable star for rear delts"],
    short_head_triceps : ["overhand bent-over cable pushdowns", "barbell skull crushers", "narrow grip Smith-machine bench presses"],
    long_head_triceps : ["neutral grip bent-over cable pushdowns", "cable star triceps pushdowns", "dumbbell skull crushers"],
    }
  };

  const generateWorkout = (option) => {
    const selectedWorkout = workoutsData[option];
    if (selectedWorkout) {
      const generatedWorkout = [];
      switch (option) {
        case "chest":
          generatedWorkout.push(
            selectedWorkout.mid_chest[
              Math.floor(Math.random() * selectedWorkout.mid_chest.length)
            ],
            selectedWorkout.upper_chest[
              Math.floor(Math.random() * selectedWorkout.upper_chest.length)
            ],
            selectedWorkout.lower_chest[
              Math.floor(Math.random() * selectedWorkout.lower_chest.length)
            ],
            selectedWorkout.inner_chest[
              Math.floor(Math.random() * selectedWorkout.inner_chest.length)
            ],
            selectedWorkout.short_head_triceps[
              Math.floor(Math.random() * selectedWorkout.short_head_triceps.length)
            ],
            selectedWorkout.long_head_triceps[
              Math.floor(Math.random() * selectedWorkout.long_head_triceps.length)
            ]
          );
          break;
        case "back":
            generatedWorkout.push(
              selectedWorkout.vertical_exercises[
                Math.floor(Math.random() * selectedWorkout.vertical_exercises.length)
              ],
              selectedWorkout.horizontal_upper_exercises[
                Math.floor(Math.random() * selectedWorkout.horizontal_upper_exercises.length)
              ],
              selectedWorkout.horizontal_lower_exercises[
                Math.floor(Math.random() * selectedWorkout.horizontal_lower_exercises.length)
              ],
              selectedWorkout.pullover_exercises[
                Math.floor(Math.random() * selectedWorkout.pullover_exercises.length)
              ],
              selectedWorkout.short_biceps_exercises[
                Math.floor(Math.random() * selectedWorkout.short_biceps_exercises.length)
              ],
              selectedWorkout.long_biceps_exercises[
                Math.floor(Math.random() * selectedWorkout.long_biceps_exercises.length)
              ]
            );
          break;
        case "legs":
          generatedWorkout.push(
            selectedWorkout.heavyQuads[
              Math.floor(Math.random() * selectedWorkout.heavyQuads.length)
            ],
            selectedWorkout.glutes[
              Math.floor(Math.random() * selectedWorkout.glutes.length)
            ],
            selectedWorkout.calves[
              Math.floor(Math.random() * selectedWorkout.calves.length)
            ],
            selectedWorkout.lightQuads[
              Math.floor(Math.random() * selectedWorkout.lightQuads.length)
            ],
            selectedWorkout.hamstrings[
              Math.floor(Math.random() * selectedWorkout.hamstrings.length)
            ]
          );
          break;
        case "shoulders":
            generatedWorkout.push(
                selectedWorkout.front_delts[
                  Math.floor(Math.random() * selectedWorkout.front_delts.length)
                ],
                selectedWorkout.mid_delts[
                  Math.floor(Math.random() * selectedWorkout.mid_delts.length)
                ],
                selectedWorkout.rear_delts[
                  Math.floor(Math.random() * selectedWorkout.rear_delts.length)
                ],
                selectedWorkout.short_head_triceps[
                  Math.floor(Math.random() * selectedWorkout.short_head_triceps.length)
                ],
                selectedWorkout.long_head_triceps[
                  Math.floor(Math.random() * selectedWorkout.long_head_triceps.length)
                ]
              );
              break;
      }
      setWorkout(generatedWorkout);
    }
  };
  
  useEffect(() => {
    generateWorkout(selectedOption);
  }, [selectedOption]);

  return (
    <div>
      <h2 className="customWorkoutHeading">Your Custom Workout</h2>
      <motion.div
        className="customWorkout"
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        exit={{ opacity: 0, y: 20 }}
        transition={{ duration: 0.3 }}
      >
        {workout.map((exercise, index) => (
          <motion.p
            key={index}
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: 20 }}
            transition={{ duration: 0.3, delay: index * 0.04 }}
          >
            {exercise}
          </motion.p>
        ))}
      </motion.div>
    </div>
  );
};

export default YourWorkout;